import type {SearchProvider, QueryEvent} from '@github-ui/query-builder-element/query-builder-api'
import {Octicon, SearchItem, FetchDataEvent} from '@github-ui/query-builder-element/query-builder-api'
import type {QueryBuilderElement} from '@github-ui/query-builder-element'

const OCTICON_MAP: {[key: string]: Octicon} = {
  repository: Octicon.Repo,
  team: Octicon.Team,
}

interface Item {
  value: string
  url: string
}

export class ItemsProvider extends EventTarget implements SearchProvider {
  priority = 1
  name = ''
  icon: Octicon
  singularItemName: string
  value: string
  type = 'search' as const

  #url: string
  #abortController: AbortController

  constructor(
    public queryBuilder: QueryBuilderElement,
    itemType: string,
  ) {
    super()

    this.icon = OCTICON_MAP[itemType] || Octicon.Default
    this.singularItemName = itemType
    this.value = itemType

    this.queryBuilder.addEventListener('query', this)
    this.queryBuilder.attachProvider(this)

    this.#url = this.queryBuilder.getAttribute('data-search-path')!
    this.#abortController = new AbortController()
  }

  abortPreviousRequests() {
    this.#abortController.abort()
    this.#abortController = new AbortController()
  }

  async handleEvent(event: QueryEvent) {
    const lastElement = event.parsedQuery.at(-1)

    if (!lastElement) return

    this.abortPreviousRequests()
    const result = this.fetchItems(event)
    this.dispatchEvent(new FetchDataEvent(result))
    const items = (await result)!
    this.emitItems(items)
  }

  async fetchItems(event: QueryEvent) {
    const filterEndpoint = new URL(this.#url, window.location.origin)
    const params = filterEndpoint.searchParams || new URLSearchParams()
    const query = event.rawQuery.toString()

    params.set('q', query)
    filterEndpoint.search = params.toString()

    let response
    try {
      response = await fetch(filterEndpoint.toString(), {
        headers: {
          Accept: 'application/json',
        },
        signal: this.#abortController.signal,
      })
    } catch (error) {
      // @ts-expect-error catch blocks are bound to `unknown` so we need to validate the type before using it
      if (error.name !== 'AbortError') {
        throw error
      }
    }

    if (!response || !response.ok) return []

    return await response.json()
  }

  emitItems(items: Item[]) {
    for (const item of items) {
      this.emitItem(item.value, item.url)
    }
  }

  emitItem(value: string, url: string) {
    this.dispatchEvent(new SearchItem({priority: this.priority, value, icon: this.icon, action: {url}}))
  }
}
